<template>
  <div class="balanceQuery">
    <!-- header -->
    <myHeader :isBack="true" title="养户服务号"></myHeader>
    <!-- head占位符 -->
    <div class="seat-head"></div>
    
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">保证金余额查询</mt-tab-item>
      <mt-tab-item id="2">对账单余额查询</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="selected">
      <!-- 保障金查询 -->
      <mt-tab-container-item id="1">
        <div class="balance">
          <div class="bg-img">
            <div class="b-title ali-c">当前余额</div>
            <div class="balance-num ali-c">{{guaranteeMoneyObj.paymentAmt}}<span>元</span></div>
          </div>
          <mt-cell title="交易明细" class="business-details">
            <div @click="handlerFilterDate('guaranteeMoney')">日期筛选▾</div>
          </mt-cell>
        </div>
        <!-- 业务类型  扣款&缴款-->
        <div class="deductions"
             v-if="guaranteeMoneyObj.security.length"
             v-for="(item,index) in guaranteeMoneyObj.security"
             :key="index">
          <mt-cell title="日期:" :value="item.bizDate"></mt-cell>
          <mt-cell title="业务类型:" :value="item.transactiontype"></mt-cell>
          <mt-cell title="金额:" :value="item.money"></mt-cell>
        </div>
        
        <NoRecord v-if="!guaranteeMoneyObj.security.length"></NoRecord>
      </mt-tab-container-item>
      
      <!-- 对账单查询 -->
      <mt-tab-container-item id="2" class="select2">
        <div class="screen">
          <mt-cell>
            <div @click="fillMouth">月份筛选▾</div>
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell @click.native="gotoBillDetails(item.rcordId)"
                   :title="item.billName"
                   :label="item.date"
                   is-link v-for="(item,index) in reconciliationList" :key="index">
            <span style="color: #888;font-size:.14rem;">详细信息</span>
          </mt-cell>
        </div>
        
        <NoRecord v-if="!reconciliationList.length"></NoRecord>
      </mt-tab-container-item>
    
    </mt-tab-container>
    
    <filter-time
      ref="filterTime1"
      @on-filter-search="onChangeFilter1">
    </filter-time>
    <mt-popup
      v-model="popupVisible"
      position="bottom"
      popup-transition="popup-fade" style="width: 100%">
      <div class="confirmDate">
        <span @click="confirmDate">确定</span>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange" style="width: 100%"></mt-picker>

    </mt-popup>


  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {queryBillRecordList, yuEQuery} from '@/api/finance'
  import FilterTime from "@/components/FilterTime";
  import NoRecord from '@/components/NoRecord'
  import { Indicator } from 'mint-ui'
  export default {
    name: 'BalanceQuery',
    data() {
      return {
        guaranteeMoneyObj: {
          paymentAmt: '',
          security: []
        },
        reconciliationList: [],
        selected: '1',
        popupVisible:false,
        slots: [
          {
            flex: 1,
            values: [],
            className: 'slot1',
            textAlign: 'right'
          }, {
            divider: true,
            content: '-',
            className: 'slot2'
          }, {
            flex: 1,
            values: ['01', '02', '03', '04', '05', '06','07','08','09','10','11','12'],
            className: 'slot3',
            textAlign: 'left'
          }
        ],
        getMouthData:''
      }
    },
    components: {
      NoRecord,
      FilterTime,
      myHeader
    },
    created() {
      this._queryBillRecordList();
      
      this._yuEQuery({
        starttime: '',
        endtime: ''
      });
      this.getYears();
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    mounted(){
    },
    methods: {
      getYears(){
        let nowDay = new Date().getFullYear();
        for (let i =0;i<10;i++){
          this.slots[0].values.push(nowDay -i);
        }
      },
      confirmDate(){
        this.popupVisible = false;
        this._queryBillRecordList(this.getMouthData);
      },
      onValuesChange(picker, values) {
        // if (values[0] > values[1]) {
        //   picker.setSlotValue(1, values[0]);
        // }
       this.getMouthData = picker.getValues().join('');
        console.log(this.getMouthData);

      },
      _yuEQuery(data) {
        yuEQuery(data).then(res => {
          Indicator.close();
          let data = res.data;
          if (data) {
            let securityList = res.data.security.map(item => {
              return {
                money: item.money,
                bizDate: item.bizDate ? item.bizDate.slice(0, item.bizDate.indexOf(' ')) : '',
                paymentDate: item.paymentDate,
                transactiontype: item.transactiontype
              }
            });
            this.guaranteeMoneyObj = {
              paymentAmt: data.paymentAmt,
              security: securityList
            }
          } else {
            this.guaranteeMoneyObj = {
              paymentAmt: data.paymentAmt,
              security: []
            };
  
            Indicator.close();
          }
          
        }).catch(err => {
          Indicator.close();
        });
      },
      _queryBillRecordList(data) {
        queryBillRecordList(data).then((res) => {
          this.reconciliationList = res.data.list.map((item, index) => {
            return {
              date: item.period,
              billName: item.batchName,
              rcordId: item.rcordId
            }
          });
          
        }, () => {
          Indicator.close();
        });
      },
      handlerFilterDate(type) {
        if (type === 'guaranteeMoney') {
          this.$refs.filterTime1.selectFilterDate();
        } else {
          this.$refs.filterTime2.selectFilterDate();
        }
      },
      onChangeFilter1({starttime, endtime}) {
        this._yuEQuery({
          starttime,
          endtime
        });
      },
      fillMouth() {
        this.popupVisible = !this.popupVisible;
      },
      gotoBillDetails(recordId) {
        // this.$router.push(`/billRecordDetails?rcordId=${recordId}`);
        this.$router.push({
          name:'billRecordDetails',
          params:{
            recordId:recordId
          }
        });
      }
    },
  }
</script>

<style scoped lang="scss">
  .balanceQuery {
    min-height: 100vh;
    background-color: #F8F8F8;
    
    .mint-cell .mint-cell-text {
      color: #333 !important;
    }
  }
  .confirmDate{
    font-size: .18rem;
    padding:0.06rem;
    span{
      float: right;
      margin-right: .2rem;
    }
  }
  .balance {
    height: 1.54rem;
    padding: .14rem .14rem 0 .14rem;
    
    .bg-img {
      width: 3.5rem;
      height: 1rem;
      color: #fff;
      border-radius: .06rem;
      background: url('~@/assets/balance-bg.png') center;
      padding: .14rem .26rem;
      
      .b-title {
        font-size: .14rem;
        height: .3rem;
      }
      
      .balance-num {
        font-size: .38rem;
        
        span {
          font-size: .16rem;
          margin-left: .1rem;
        }
      }
    }
    
    .mint-cell {
      background-color: #F8F8F8 !important;
    }
    
    .mint-cell /deep/ .mint-cell-wrapper /deep/ .mint-cell-value {
      font-size: .12rem;
      color: #444;
      background-color: #fff;
      height: .22rem;
      border-radius: .22rem;
      padding: 0 .1rem;
    }
    
    .mint-cell /deep/ .mint-cell-wrapper {
      font-size: .14rem;
      
    }
  }
  
  .deductions {
    margin-top: .1rem;
  }
  
  .business-details {
    border-bottom: 0;
  }
  
  .screen {
    .mint-cell {
      background-color: #F8F8F8 !important;
    }
    
    .mint-cell /deep/ .mint-cell-wrapper /deep/ .mint-cell-value {
      font-size: .12rem;
      color: #444;
      background-color: #fff;
      height: .22rem;
      border-radius: .22rem;
      padding: 0 .1rem;
    }
  }
  
  .list /deep/ .mint-cell {
    height: .7rem !important;
    
    .mint-cell-wrapper {
      height: .7rem !important;
      
      .mint-cell-label {
        font-size: .14rem;
      }
    }
  }
  
  .mint-cell /deep/ .mint-cell-wrapper {
    font-size: .16rem;
    color: #333;
  }
  
  .mint-cell /deep/ .mint-cell-wrapper /deep/ .mint-cell-value {
    color: #333;
  }
  
  .mint-tab-item /deep/ .mint-tab-item-label {
    font-size: .16rem;
  }
  
  .mint-navbar /deep/ .mint-tab-item {
    padding: .12rem 0;
    color: #8C9198;
  }
  
  .mint-navbar /deep/ .mint-tab-item.is-selected {
    border-bottom: 1px solid #000;
    color: #000;
    margin-bottom: 1px;
  }
</style>




